@charset "UTF-8";
body {
  background-color: #fefbf8;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh; }

.pageWrapper {
  display: block;
  margin: auto;
  max-height: 36rem;
  max-width: 80rem;
  text-align: center; }
  @media (max-width: 1337px) {
    .pageWrapper {
      padding: 5%;
      padding-top: 0px; } }

.locationUnavailableTex {
  color: tomato;
  position: initial;
  display: flex; }

.customTitle {
  font-family: Monoton;
  color: #edacb8;
  font-size: 300%;
  padding-bottom: 5px;
  border-bottom: 1px solid #edacb8;
  display: inline-block;
  cursor: pointer; }
  .customTitle:hover,
  .customTitle :active,
  .customTitle :focus {
    color: #e99baa; }

.map {
  z-index: 1;
  height: 30rem;
  border-radius: 20px; }
  @media (max-width: 450px) {
    .map {
      height: 20rem; } }

.wikiTitleContainer {
  display: flex;
  justify-content: space-between; }

.wikiTitle {
  padding-left: 1rem;
  padding-right: 1rem; }
  @media (max-width: 450px) {
    .wikiTitle {
      font-size: larger;
      max-width: 220px;
      padding-left: 0;
      padding-right: 0; } }

.previous {
  display: flex;
  flex-grow: 1;
  align-items: center;
  text-align: left;
  font-size: x-large;
  color: gray;
  cursor: pointer; }
  .previous:hover {
    filter: brightness(50%);
    animation: to-right 1s normal forwards; }

@keyframes to-right {
  50% {
    margin-right: -10px;
    margin-left: 10px; } }

.next {
  display: flex;
  flex-grow: 1;
  color: gray;
  align-items: center;
  direction: rtl;
  font-size: x-large;
  cursor: pointer; }
  .next:hover,
  .next :active {
    filter: brightness(50%);
    animation: to-left 1s normal forwards; }

@keyframes to-left {
  50% {
    margin-right: 10px;
    margin-left: -10px; } }

*:before,
*:after {
  content: "";
  position: absolute; }

.buttonWrapper {
  text-align: center; }

.wikiLink {
  padding-bottom: 2rem;
  text-align: "center";
  display: "block";
  width: "2rem";
  height: "2rem"; }
  .wikiLink:hover,
  .wikiLink :active,
  .wikiLink :focus {
    filter: brightness(90%); }

button {
  background: transparent;
  color: rgba(128, 128, 128, 0.609);
  border: 3px solid rgba(128, 128, 128, 0.644);
  border-radius: 50px;
  padding: 0.8rem 2rem;
  font: 24px "Margarine", sans-serif;
  outline: none;
  cursor: pointer;
  position: relative;
  transition: 0.2s ease-in-out;
  letter-spacing: 2px;
  margin-top: 5rem;
  z-index: 1; }
  button:hover,
  button :active,
  button :focus {
    color: white;
    background: rgba(128, 128, 128, 0.609);
    border: 3px solid rgba(128, 128, 128, 0.244); }

.button-hand div:before {
  content: "ðŸ‘‡";
  display: flex;
  align-items: center;
  font-size: 60px;
  transform: scaleX(-1);
  z-index: 5;
  right: 17%;
  top: -4.5rem;
  animation: up-down 1s infinite; }

@keyframes up-down {
  50% {
    margin-top: -20px; } }
